import React, {Component} from "react";
import ErrorRow from "./ErrorRow";
import {excludeProps, loadModule, styleVariant} from "eshop/modules/core/utils/ui";

var moduleId = 0; //All forms with autocomplete should be in one bundle!
class LabeledInput extends Component {
    constructor(props) {
        super(props);

        //props that should not be passed to input
        this.OWN_PROPS = ["showMaskOnHover", "virtualKeyboard", "wrapperClassName","virtualKeyboardWrapperClass", "onChange", "onBlur", "placeholder", "mask", "errors", "validateEmpty", "valid", "labelType", "labelClassName", "validationMark", "mapSuggestion", "markClassName", "locked", "labelElement", "customInputClass"];
        if(props.autoComplete) {
            this.OWN_PROPS = this.OWN_PROPS.concat(["maxResults", "minLength", "mapSuggestion", "suggestions"]);
            this.id = props.id || "react-autocomplete-" + moduleId++;
        } else {
            this.id = props.id;
        }

        //initializing validation sign display
        this.showValidationSign  = !!props.defaultValue || !!props.value || (!!props.errors && props.errors.length > 0);
        this.forceHideSign = false;

        //initializing state
        this.state = {switchState: true};

        this.handleVirtualKeyboardChange=this.handleVirtualKeyboardChange.bind(this);
        this.virtualKeyboardId='LabeledInput-virtualKeyboard' + moduleId++;
    }

    componentDidMount() {
        //start label module
        if (this.props.labelType) this.initUiLabel(this.props.labelType);
        if (this.props.labelType) this.OWN_PROPS = this.OWN_PROPS.concat(["labelClassName", "label"]);

        //start mask module
        if (this.props.mask && this.props.placeholder !== undefined) {
            this.initMaskModule();
        }
        if(this.isVirtualKeyboardEnabled()){
            this.initVirtualKeyboardModule();
        }
        //start autocomplete module
        if(this.props.autoComplete) {
            this.modulePromise = loadModule(this.refs.input, {
                path: "common/modules/opl-autocomplete",
                options: {
                    noWrap: true,
                    arrayData: true,
                    minLength: this.props.minLength,
                    maxResults: this.props.maxResults,
                    completecallback: this.onSelect.bind(this)
                }
            });
            this.updateSuggestions();
        }
    }

    handleVirtualKeyboardChange(value){
        let name=this.props.name;
        let id="";
        this.handleValueChange(id,name, value);
        this.handleBlur(id,name,value);
        this.refs.input.focus();
    }
    initVirtualKeyboardModule() {
        loadModule(this.refs.virtualKeyboardWrapper, {path:'common/modules/opl-keyboard'});
        OPL.UI.on(OPL.UI.EVENTS.modules.virtualKeyboard.valueChanged, this.handleVirtualKeyboardChange, this.virtualKeyboardId);
    }
    isVirtualKeyboardEnabled(){
        return this.props.virtualKeyboard && !(this.props.autoSelect || this.props.disabled);
    }

    updateSuggestions() {
        //TODO: use the promise when OPL.UI.off is fixed and doesn't recognize a handler only by its source code
        //this.modulePromise = this.modulePromise.then(() => {
            if (!this.props.suggestions) return;
            let suggestions = this.props.suggestions.map(this.props.mapSuggestion);
            OPL.UI.fire(OPL.UI.EVENTS.modules.autocomplete.arrayData, suggestions, this.id);
        //});
    }

    //Method to select suggestion - autocomplete only
    onSelect() {
        if (this.props.onChange) {
            this.showValidationSign = true;
            this.forceHideSign = false;
            let {id, name, value} = this.refs.input;
            this.props.onChange({id, name, value});
        }
    }

    //Update component despite of state changing
    switchState() {
        this.setState({switchState: !this.state.switchState});
    }

    componentWillUpdate(nextProps) {
        const nextPropsErrors = !!nextProps.errors && nextProps.errors.length > 0 && !nextProps.disabled;
        const prevPropsErrors = !!this.props.errors && this.props.errors.length > 0 && !this.props.disabled;
        if(nextPropsErrors || (!this.props.value && nextProps.value)) {
            this.showValidationSign = true;
        } else if(prevPropsErrors && !nextPropsErrors && (nextProps.value !== undefined && !nextProps.value)) {
            //Undefined means uncontrolled.
            this.showValidationSign = false;
        }
        //Hide validation sign when clear input
        if(this.props.value && !nextProps.value) {
            this.showValidationSign = false;
        }
    }

    componentDidUpdate(prevProps) {
        //update label module
        if (this.props.labelType && !prevProps.value && this.props.value) {
            if (this.props.labelType !== "floating") {
                // TODO Stopping all modules is inefficient, but we can't stop a specific module now - see issue NOR-56126
                OPL.UI.stopModules(this.refs.wrapper);
                OPL.UI.initModules(this.refs.wrapper);
            } else {
                // TODO checkvalue event for sliding label is not available
                OPL.UI.fire(OPL.UI.EVENTS.modules.floatinglabels.checkvalue, this.refs.input, this.refs.wrapper);
            }
        }

        //update suggestions - autocomplete only
        if(this.props.autoComplete && prevProps.suggestions !== this.props.suggestions) {
            this.updateSuggestions();
        }
    }

    initUiLabel(type) {
        loadModule(this.refs.wrapper, {path: "core/modules/" + type + "-label"});
    }

    initMaskModule() {
        loadModule(this.refs.input, {
              path:'common/modules/opl-input-mask',
              options: {
                "mask": this.props.mask,
                "placeholder": this.props.placeholder,
                "showMaskOnHover": this.props.showMaskOnHover
              }
        });
    }
    isVirtualKeyboardEnabled(){
        return this.props.virtualKeyboard && !(this.props.autoSelect || this.props.disabled);
    }

    handleChangeEvent(event) {
        this.handleValueChange(event.target.id, event.target.name, event.target.value);
    }
    handleValueChange(id, name, value){
        let index = this.props.index;
        if(!this.forceHideSign) {
            this.switchState();
        }
        this.forceHideSign = true;
        if(this.props.onChange) {
            if(index !== undefined){
                this.props.onChange({id, name, value, index});

            }else{
                this.props.onChange({id, name, value});
            }
        }

    }

    clearFocus() {
        this.refs.input.blur();
    }
    handleBlurEvent(event){
        this.handleBlur(event.target.id, event.target.name, event.target.value);
    }

    handleBlur(id, name, value) {
        let index = this.props.index;
        this.forceHideSign = false;
        this.showValidationSign = true;
        this.switchState();
        if(this.props.onBlur) {
            if(index !== undefined){
                this.props.onBlur({id, name, value, index});
            }else {
                this.props.onBlur({id, name, value});
            }
        }
    }

    render() {
        let renderValidation = !this.forceHideSign && this.showValidationSign;
        //Input, validation sign and label wrapper
        let virtualKeyboardWrapperClass = "";
        let wrapperClassName = styleVariant("o-", this.props.labelType, "-label ") + (this.props.wrapperClassName || "");
        //Label
        let labelClassName = (this.props.labelClassName || "") + styleVariant(" o-", this.props.labelType, "-label__placeholder");
        //input
        let inputClassName = (this.props.className || "");
        //Validation sign
        let markClassName = (this.props.markClassName || "") + " o-validation-mark";
        if (this.props.labelType && (renderValidation || !!this.props.value))
            inputClassName += " is-not-empty";
        if (this.props.validationMark && renderValidation) {
            if (this.props.valid === true) {
                inputClassName += " success";
                markClassName += " o-validation-mark--success"
            } else if (this.props.valid === false) {
                inputClassName += " error";
                markClassName += " o-validation-mark--error"
            } else if (this.props.validateEmpty && (this.props.value !== undefined && !this.props.value)) {
                inputClassName += " error";
                markClassName += " o-validation-mark--error"
            }
        }

        //autocomplete hints styling
        if(this.props.autoComplete && wrapperClassName.indexOf("opl-autocomplete") < 0) {
            wrapperClassName += " opl-autocomplete";
        }
        if(this.isVirtualKeyboardEnabled()){
            virtualKeyboardWrapperClass +=" opl-keyboard";
            inputClassName +=" opl-keyboard-input-value";
        }

        return (
            <ErrorRow showErrors={renderValidation} errors={this.props.errors} className={this.props.errorWrapperClassName || ""}>
                <div className={virtualKeyboardWrapperClass} ref="virtualKeyboardWrapper" id={this.virtualKeyboardId}>
                    <div className={wrapperClassName} ref="wrapper" >
                        <div className="o-floating-label">
                            <input {...excludeProps(this.props, this.OWN_PROPS)}
                            autoComplete="off"
                            ref="input"
                            id={this.id}
                            className={inputClassName} aria-describedby={(this.props.id || this.props.label) + "__placeholder"}
                            disabled={this.props.autoSelect || this.props.disabled}
                            onBlur={this.handleBlurEvent.bind(this)}
                            onChange={this.handleChangeEvent.bind(this)}/>

                            {this.props.validationMark && <span className={markClassName} />}
                            {this.props.labelType &&
                                <p id={(this.props.id || this.props.label) + "__placeholder"} className={labelClassName}>
                                    {this.props.label}
                                </p>
                            }
                            {!!this.props.labelElement && this.props.labelElement}
                        </div>
                    </div>
                </div>
            </ErrorRow>
        );
    }
}

LabeledInput.defaultProps = {
    labelType: "floating",
    labelClassName: "label",
    validationMark: true,
    minLength: 3,
    mapSuggestion: (suggestion) => ({value: suggestion, label: suggestion}),
    showMaskOnHover: false,
    virtualKeyboard: false
};

export default LabeledInput;
